/* List three ways to select html elements in css */
/*
    1- using element ID attribute e.g. #element-id-name
    2- using element Class Name e.g. .element-class-name
    3- using global selector by typing element tag name e.g. div
*/

body {
  background-color: #eee;
  padding: 0;
  margin: 0;
  font-family: Arial;
}

header {
  background: url('https://drive.google.com/uc?id=1ADdMAd8sSM1GxabjRUpJaqliyYQMxixu');
  background-size: cover;
  background-position: center;
  color: #ccc;
  display: flex;
  /* What does 'display: flex' do?*/
  /*
    it changes the display property of the selected element to be show in FlexBox way, in other words, the element will be responsive (it helps to design responsive web site)
  */
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.logo {
  width: 100px;
  margin: 40px;
  border-radius: 50%;
}

.title {
  margin: 40px;
  padding: 15px;
  border: solid #ccc 5px;
  border-radius: 15px;
}

.italic {
  margin: 40px;
  font-size: 24px ;
  /* px, _____, and _____ are 3 measurements units in CSS */
  /* px, em, and in are 3 measurements units in CSS */
  font-style: italic;
  font-family: Tahoma;
  text-align: center;
}

.bootcamp {
  padding: 20px;
}

.align-right {
  text-align: right;
}

.bootcamp p,
li {
  margin: 10px;
}

#awesome {
  display: none;
}

footer {
  display: flex;
  justify-content: space-between;
  padding: 10px;
}

#searchBox {
  margin: 0 auto;
  display: inline-block;
  font-size: 16px;
  padding: 5px;
  border-radius: 5px;
  border-color: #dda65f;
  outline-color: #3a7ddd;
}

#searchBox::selection {
  border-color: #3a7ddd;
}

#infoBtn,
#searchBtn,
#awesomeBtn {
  background-color: #dda65f;
  /* add border color to match the background color */
  /* border-color already has been added before but it will not work because we need to add style and width of the border to make it appear */
  border-color: #dda65f;
  color: white;
  margin: 0 auto;
  display: inline-block;
  font-size: 16px;
  padding: 5px 15px;
  border-radius: 5px;
  outline-color: #3a7ddd;
}

/* Make the mouse curser look like a hand when you hover over the buttons (#infoBtn, #searchBtn, #awesomeBtn) and add backgound and border color of #3a7ddd*/
#infoBtn:hover,
#searchBtn:hover,
#awesomeBtn:hover{
    cursor: pointer;
    background-color: #3a7ddd;
    border-color: #3a7ddd;
}

#awesome > p{
  margin-top: 0;
}

.socialIcon {
  width: 25px;
  margin: 0 10px;
}

ul{
  padding-inline-start: 0;
}

.buttons{
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-around;
  align-items: center;
  padding: 20px 0 40px 0;
}

.buttons > div{
  align-self: start;
  display: flex;
  flex-direction: column;
  flex: 1;
  justify-content: center;
  align-items: center;
}

